.user{
    display: flex;
    align-items: center;
    width: 100%;
    height: 32px;
    font-size: 20px;
    font-weight: 600;
    color: #393e46;
    text-decoration: none;
    transition: color .3s;
}

.user i {
    font-size: 32px;
    margin: 0 12px 0 0;
}

.user:hover{
    color: #ca3e47;
}