.input{
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 500px;
    height: 32px;
    border: 1px solid lightgrey;
    border-radius: 20px;

    input{
        width: calc(100% - 32px);
        height: 32px;
        background: transparent;
        border: 0;
        padding: 0 10px;
        outline: 0;
    }

    button{
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: transparent;
        border: 0;
        font-size: 26px;
        color: grey;
        transition: color .2s;

        &:hover{
            :hover{
                color: #ca3e47
            }
        }
    }

    .resQuery{
        position: absolute;
        z-index: 1;
        top: 60px;
        width: 100%;
        height: 300px;
        background-color: white;
        border: 1px solid grey;
        border-radius: 5px;

        .result{
            width: 100%;
        }
    }
}

@media screen and (max-width: 680px) {
    .input{
        width: calc(100% - 120px);

        .resQuery{
            width: calc(100vw - 60px);
            left: -60px;
        }
    }   
}

